.esteban-regular {
  font-family: "Esteban", serif;
  font-weight: 400;
  font-style: normal;
}

:root {
  --talesoftheoldwest-c-dark: #191813;
  --talesoftheoldwest-c-faint: #c9c7b8;
  --talesoftheoldwest-c-beige: #b5b3a4;
  --talesoftheoldwest-c-tan: #444444;
  --talesoftheoldwest-c-white: #ffffff;
  --talesoftheoldwest-c-black: #000000;
  --talesoftheoldwest-c-groove: #eeede0;
}

.theme-dark {
  --talesoftheoldwest-c-dark: #191813;
  --talesoftheoldwest-c-faint: #c9c7b8;
  --talesoftheoldwest-c-beige: #b5b3a4;
  --talesoftheoldwest-c-tan: #444444;
  --talesoftheoldwest-c-white: #ffffff;
  --talesoftheoldwest-c-black: #000000;
  --talesoftheoldwest-c-groove: #eeede0;
}

.talesoftheoldwest .chat.roll {
  color: var(--black);
  font-family: 'Esteban", serif';
  font-size: 12px;
  line-height: 16px;
  padding: 5px;
}

.talesoftheoldwest .chat.roll .foreground {
  padding: 5px;
}

.talesoftheoldwest .chat.roll .border {
  border: 1px solid transparent;
  border-color: var(--color-border);
  border-image: var(--chat-roll-border) 20/10px;
}

.talesoftheoldwest .chat.roll strong {
  text-transform: uppercase;
  font-weight: bold;
  color: var(--talesoftheoldwest-c-beige);
  font-size: 14px;
}

.talesoftheoldwest .chat.roll h3 {
  font-family: 'Esteban", serif';
  font-size: 14px;
  font-weight: bold;
  color: var(--talesoftheoldwest-c-beige);
  margin: 0 0 8px;
  text-align: center;
  border-bottom: 1px solid var(--color-border);
}

.talesoftheoldwest .chat.roll h4 {
  font-family: 'Esteban", serif';
  font-size: 16px;
  font-weight: bold;
  margin: 10px 0 10px 0;
  text-align: center;
}

.talesoftheoldwest .chat.roll p {
  font-size: 16px;
  font-weight: bold;
  margin: 0 0 4px;
}

.talesoftheoldwest .chat.roll .tooltip {
  position: relative;
  display: inline-block;
  border-bottom: 1px dotted black;
}

/* Tooltip text */
.talesoftheoldwest .chat.roll .tooltip .tooltiptext {
  visibility: hidden;
  width: 10em;
  background-color: var(--black);
  color: var(--white);
  text-align: center;
  padding: 5px 0;
  border-radius: 6px;
  /* Position the tooltip text - see examples below! */
  position: absolute;
  z-index: 1;
}

.talesoftheoldwest .chat.roll .right {
  top: -15%;
  left: 105%;
}

.talesoftheoldwest .chat.roll .tooltip:hover .tooltiptext {
  visibility: visible;
}

.talesoftheoldwest .chat.roll img,
.talesoftheoldwest .chat.roll .portrait {
  border: 0;
  /* display: block; */
  margin: 0 auto 8px;
  height: 40px;
  width: auto;
}

.talesoftheoldwest .dice-push {
  flex: 0 0 22px;
  font-size: 12px;
  text-align: center;
}

.chat-message {
  font-family: 'Esteban", serif';
  color: black;
  background-image: url("../assets/background/lapp.webp");
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: auto; /* Resize the background image to cover the entire container */
  background-origin: content-box;
}

/* Global styles */
.window-app {
  font-family: 'Esteban", serif';
}

.rollable:hover, .rollable:focus {
  color: #000;
  text-shadow: 0 0 10px red;
  cursor: pointer;
}

.dice-tooltip .roll > img {
  width: 24px;
}

body .talesoftheoldwest.application {
  font-family: 'Esteban", serif';
  color: black;
  width: 600;
  height: 896;
}
body.theme-light .application {
  background-image: url("../assets/background/lapp.webp");
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: 100% 100%;
  border: unset;
  filter: drop-shadow(8px 12px 8px #353232) !important;
  box-shadow: unset;
}
body.theme-dark .application {
  background: url("../assets/background/lapp.webp");
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-origin: content-box;
  border: unset;
  filter: drop-shadow(8px 12px 8px #353232) !important;
  box-shadow: unset;
}
body.theme-light .dialog .window-content {
  color: black;
  background: unset;
  background-image: url("../assets/background/lapp.webp");
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: cover; /* Resize the background image to cover the entire container */
  background-origin: content-box;
}
body.theme-light .dialog .window-content .form-footer button {
  min-width: auto;
  width: fit-content;
}
body.theme-dark .dialog .window-content {
  color: black;
  background: unset;
  background-image: url("../assets/background/lapp.webp");
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: cover; /* Resize the background image to cover the entire container */
  background-origin: content-box;
}
body.theme-dark .dialog .window-content .form-footer button {
  min-width: auto;
  width: fit-content;
}
body .application input[type=text],
body .application input[type=number],
body .application select {
  background: white;
  color: black;
}

.application .window-header {
  background-image: url("../assets/background/header-plank.webp");
}

.application .window-resize-handle {
  position: absolute;
  inset: auto -2px -3px auto;
